
.color-picker-modal .modal-mask{position:fixed;z-index:9998;top:0;left:0;width:100%;height:100%;background-color:rgba(0,0,0,0.5);display:table;transition:opacity .3s ease
}
.color-picker-modal .modal-wrapper{display:table-cell;vertical-align:middle
}
.color-picker-modal .modal-container{width:300px;margin:0px auto;padding:20px 30px;background-color:#fff;border-radius:2px;box-shadow:0 2px 8px rgba(0,0,0,0.33);transition:all .3s ease;font-family:Helvetica, Arial, sans-serif
}
.color-picker-modal .modal-body{margin:20px 0
}
.color-picker-modal .modal-enter{opacity:0
}
.color-picker-modal .modal-leave-active{opacity:0
}
.color-picker-modal .modal-enter .modal-container,.color-picker-modal .modal-leave-active .modal-container{transform:scale(1.1)
}
.color-picker-modal *:focus{outline:none
}
.color-picker-modal .vue-swatches__container:focus,.color-picker-modal .vue-swatches__wrapper:focus{outline:none
}
.color-picker-modal .vue-swatches .vue-swatches__wrapper{display:-ms-flexbox;display:flex;max-width:100%;-ms-flex-wrap:wrap;flex-wrap:wrap;-ms-flex-pack:center;justify-content:center;padding:0 !important
}
.color-picker-modal .vue-swatches__container .vue-swatches__wrapper .vue-swatches__swatch{min-width:42px;display:block;margin-right:5px !important;margin-left:5px
}
.color-picker-modal .vue-swatches__container .vue-swatches__wrapper .vue-swatches__swatch .vue-swatches__check__wrapper{margin-top:9px
}
.color-picker-modal .vue-swatches__container .vue-swatches__fallback__input{background:unset;color:black
}
.color-picker-modal .vue-swatches__container .vue-swatches__fallback__button{display:none
}



