
.editable-table{background-color:#3b4348
}
.editable-table .scroller{height:calc(50vh - 151px);margin-top:46px
}
.editable-table .scroller>.vue-recycle-scroller__slot{margin-top:-46px
}
.editable-table .headers{font-size:14px;margin-bottom:auto;margin-top:auto;min-width:144px;max-width:144px;font-weight:700;padding-left:10px;background-color:#3b4348;color:#bbc3c7;text-align:center;word-break:break-word
}
.editable-table .flex-row{display:-ms-flexbox;display:flex
}
.editable-table .vue-recycle-scroller.ready.direction-vertical .vue-recycle-scroller__item-view{display:-ms-flexbox;display:flex
}
.editable-table .checkbox-header{background-color:#3b4348;min-width:50px
}
.editable-table .vue-recycle-scroller__item-wrapper{margin-top:20px;margin-bottom:90px;overflow:visible
}
.editable-table .fixed{position:fixed;height:25px
}
.editable-table #fixedHeader{position:fixed;background-color:#3b4348;z-index:100;height:46px;padding-right:100vw
}
.editable-table .vue-recycle-scroller__slot{height:10px
}
.editable-table .vue-recycle-scroller.ready.direction-vertical .vue-recycle-scroller__item-view{width:auto !important;margin-top:20px
}
.editable-table .vue-recycle-scroller__item-wrapper{position:static !important
}
.editable-table .vue-recycle-scroller__slot{padding-bottom:15px
}
.editable-table .active{color:#409eff
}
.editable-table .table-data{margin-bottom:auto;margin-top:auto;max-width:134px;min-width:134px;padding-left:10px;padding-right:10px;text-align:center
}
.editable-table .table-first-column{max-width:204px;min-width:204px;padding-right:0
}
.editable-table .cell{text-align:center;word-break:break-word;color:#bbc3c7
}
.editable-table .cell input{width:100%;background-color:#3b4348;color:#bbc3c7;padding-top:5px;padding-bottom:5px;margin-top:5px;margin-bottom:5px;height:16px;font-size:16px
}
.editable-table .select{margin-bottom:auto;margin-top:auto;max-width:50px;min-width:50px
}
.editable-table .destroy,.editable-table .duplicate{width:35px;margin:0 auto;padding:0 10px 0 10px;text-align:center
}
.editable-table .destroy>[type="radio"],.editable-table .duplicate>[type="radio"]{width:25px
}
.editable-table .destroy>svg,.editable-table .duplicate>svg{margin-top:12px;margin-left:-6px
}
.editable-table .duplicate svg{margin-top:2px;width:20px
}



